import React, { useState } from 'react';
import '../styles/PhotoGallery.css';

function PhotoGallery({ images }) {
  const [visibleIndex, setVisibleIndex] = useState(0); //lk etapta grnen resimlerin banda olan fotorafnn 0. olan olmasn salar.
  const [selectedImage, setselectedImage] = useState(images[0]); //stne tklandnda byyecek resmi tutar. (Balangta 0. olan resimdir.)
  const [modal, setModal] = useState(false); //stne tklandnda modaln almasn salar, ilk etapta modal kapal

  const ClickingImage = (img) => { //Grsele tkladnda modal olarak resmin almasn salar
    setselectedImage(img);
    setModal(true);
  };

  const scrollingImage = (direction) => {
    let newIndex = visibleIndex + (direction === 'left' ? -1 : 1);
    setVisibleIndex(newIndex);
  };

  const closeModal = () => {
    setModal(false);
  };

  return (
    <div className="gallery">
      {modal && (
        <div className="modal" onClick={closeModal}>
          <div className="close">x</div>
          <img src={selectedImage} alt="Seili" className="Selected-Modal-Content"/>
        </div>
      )}

      <button className="scroll-button left" onClick={() => scrollingImage('left')} disabled={visibleIndex === 0}>
        &lt;
      </button>

      <div className="img-scrollingImage">
        <div className="scrollingImage-container" style={{ transform:`translateX(${-visibleIndex * (100/3)}%)` }}>
          {images.map((img, index) => (
            <img
              key={index}
              src={img}
              alt={`Resim ${index}`}
              className="scrollingImage"
              onClick={() => ClickingImage(img)}
            />
          ))}
        </div>
      </div>

      <button className="scroll-button right" onClick={() => scrollingImage('right')} disabled={visibleIndex === images.length-3}>
        &gt;
      </button>
    </div>
  );
}

export default PhotoGallery;
